<template>
  <div class="goodsShelvesDetails">
    <AppHeader>
      <p slot="center">棚格图</p>
    </AppHeader>

    <div style="height: 45px;">
      <div class="search-header-cont">
        <div class="search-header bg-fff">
          <van-search
            placeholder="请输入关键词搜索"
            shape="round"
            v-model="searchKey"
            @search="confirmSearch"
          />
        </div>
      </div>
    </div>

    <div class="form-page">
      <div class="filters">
        <div class="filters-label">货架编号:</div>
        <div class="date-filters">
          <p class="text-left">{{ code }}</p>
        </div>
      </div>

      <div class="filters">
        <div class="filters-label">图片:</div>
        <div class="date-filters">
          <img :src=" baseURL + '/img/' + imagePath" v-if="imagePath" />
        </div>
      </div>
    </div>

    <div class="detailed-table" v-if="hasData">
      <div class="table-content">
        <table>
          <thead>
            <tr>
              <th>序号</th>
              <th>部门编号</th>
              <th>商品名称</th>
              <th>第二代码</th>
              <th>中类Code</th>
              <th>排面数</th>
              <th>陈列数</th>
              <th>操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(items, index) in currentData" :key="index">
              <td>{{ index + 1 }}</td>
              <td>{{ items.code }}</td>
              <td>{{ items.name }}</td>
              <td>{{ items.code2 }}</td>
              <td>{{ items.sort }}</td>
              <td>{{ items.rowNumber }}</td>
              <td>{{ items.columnVolume }}</td>
              <td>
                <div class="operation">
                  <van-button class="edit" type="info" url>编辑</van-button>
                  <van-button class="delete" type="info" @click="deleteThis">删除</van-button>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>

    <div class="pagination-module" v-if="hasData">
      <el-pagination
        background
        hide-on-single-page
        layout="prev, pager, next, jumper"
        :pager-count="5"
        :page-size="10"
        :total="recordcount"
        :current-page="currentPage"
        @current-change="pageCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import {
  Search,
  Cell,
  CellGroup,
  Icon,
  Field,
  DatetimePicker,
  Button,
  Popup
} from "vant";

import ElementUI from "element-ui";

Vue.use(Search)
  .use(Cell)
  .use(CellGroup)
  .use(Field)
  .use(DatetimePicker)
  .use(Popup)
  .use(Button)
  .use(Icon);
Vue.use(ElementUI);

// @ is an alias to /src
import AppHeader from "@/components/AppHeader.vue";
import "element-ui/lib/theme-chalk/index.css";

export default {
  name: "GoodsShelvesDetails",
  components: {
    AppHeader
  },

  data() {
    return {
      searchKey: "",
      code: "",
      imagePath: "",
      hasData: false,
      resultData: [],
      recordcount: [],
      currentPage: 1,
      recordcount: ""
    };
  },
  mounted() {
    this.get_defaultData();
    this.get_data();
  },
  methods: {
    confirmSearch(val) {
      this.searchKey = val;
      this.code = val;
      this.get_data();
    },
    pageCurrentChange(val) {
      console.log(val);
      this.currentPage = val;
      this.get_data();
      console.log(this.currentPage);
    },
    get_defaultData() {
      this.baseURL = this.$store.getters.getBaseURL;
    },
    // 获取数据
    get_data() {
      this.toast.loading({
        message: "加载中...",
        forbidClick: true,
        loadingType: "spinner",
        duration: 0
      });
      this.getStoreInfo = this.$store.getters.getStoreInfo;
      this.serverHost
        .fetchGet("/racktable/getRackTableBySgidAndCode", {
          sgid: this.getStoreInfo.gid,
          code: this.code
        })
        .then(res => {
          console.log(res);
          if (res.data.code == 200) {
            if (res.data.data) {
              this.imagePath = res.data.data.imagePath;
            } else {
              this.imagePath = "";
            }
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
      this.serverHost
        .fetchGet("/racktable/getRackTableAndGoodsByCode", {
          code: this.code
        })
        .then(res => {
          this.toast.clear();
          console.log(res);
          if (res.data.code == 200) {
            const resultData = res.data.data;
            if (resultData.length == false) {
              this.hasData = false;
            } else {
              this.hasData = true;
              this.resultData = resultData;
              this.currentData = resultData.slice(
                this.currentPage * 10 - 10,
                this.currentPage * 10
              );
              this.recordcount = resultData.length;
              console.log(this.currentData);
            }
          }
        })
        .catch(res => {
          this.toast.clear();
          console.log(res);
        });
    },
    deleteThis() {}
  }
};
</script>

